<template lang="pug">
    .schedule__container
        h4.schedule__title Расписание
        ul.schedule__list
            li.schedule__item(v-for="item,idx in days") 
                .schedule__day {{item}}
                ul.schedule__content
                    li.schedule__point(v-for="time,index in times") 
                        schedule-point-container(
                            :time='time'
                            :day='idx+1'
                            v-if='scholarGroups.filter(ele=>ele.schedule.filter(item=>item.scheduleDay==idx+1&&item.scheduleTime==index+1).length!=0).length!=0'
                            :group='scholarGroups.filter(ele=>ele.schedule.filter(item=>item.scheduleDay==idx+1&&item.scheduleTime==index+1).length!=0)[0]'
                        )
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
    components: {
        schedulePointContainer: () => import("../../teacher/schedule/schedule-point-container")
    },
    created() {
        this.fetchScholarGroupsWithSchedule(this.user.id)
    },
    data () {
        return {
            days: ['Понедельник','Вторник','Среда','Четверг','Пятница','Суббота','Воскресенье'],
            times: [
                '08.00-09.00', '09.00-10.00', '10.00-11.00', '11.00-12.00', '12.00-13.00',
                '13.00-14.00', '14.00-15.00', '15.00-16.00', '16.00-17.00',
                '17.00-18.00'
            ]       
        }
    },
    computed: {
        ...mapState ("user", {
            user: state => state.user}),
        ...mapState ("scholarGroups", {
            scholarGroups: state => state.scholarGroups})
    },
    methods: {
        ...mapActions ("scholarGroups", ["fetchScholarGroupsWithSchedule"])
    }
}
</script>
